<script>
import axios from 'axios'

export default {
    name: 'mraLogin',
    data() {
        return {
            loginSuccess: false,
            loginError: false,
            user: '',
            password: '',
            error: false
        }
    },
    methods: {
        async login() {
            try {
                const result = await axios.get('/login', {
                    auth: {
                        id: this.id,
                        pw: this.pw
                    }
                });
                if (result.status === 200) {
                    this.loginSuccess = true
                }
            } catch (err) {
                this.loginError = true;
                throw new Error(err)
            }
        }
    }
}

</script>
